function Icon({ type, size = '12', inline = false, inverted = false, iconButton = false }) {
  const margin = inline ? '0 3px' : 0;
  const fillColor = inverted ? '#fdfffc' : '#282c34';
  const transform = iconButton ? 'translate(-5px, -2px)' : 'translateY(-2px)';

  const styles = {
    svg: {
      display: 'inline-block',
      verticalAlign: 'middle',
      margin,
      transform: transform,
    },
    path: {
      fill: fillColor,
    },
  };

  return (
    <svg
      title={type}
      style={styles.svg}
      width={`${size}px`}
      height={`${size}px`}
      viewBox="0 0 520 520"
    >
      <path
        style={styles.path}
        d={ICONS?.[type] ?? ICONS.default}
        className={`icon-${type}`}
        data-testid="icon-path"
      >
        <title>{type}</title>
      </path>
    </svg>
  );
}

export default Icon;
